const Zlib = require('./lib/unzip.min').Zlib;
const path = require('path');
const mime = require('mime-type');

const decoder = new TextDecoder();

const composedConfiguration = {
  configurations: []
};

const baseConfiguration = {
  delay: 0,
  isRgx: false,
  label: '',
  method: 'GET',
  requestPayload: '',
  responseHeaders: [
    {
      active: true,
      header: 'Content-Type',
      id: '',
      value: ''
    }
  ],
  responsePayload: '',
  status: 200,
  url: ''
};

document.addEventListener('DOMContentLoaded', () => {
  const fileForm = document.getElementById('file_input');
  const composeButton = document.getElementById('compose_button');

  fileForm.addEventListener('change', (event) => {
    composeButton.disabled = false;
    const file = fileForm.files[0];

    const zipReader = new FileReader();
    zipReader.onload = function () {
      const zipArr = new Uint8Array(zipReader.result);
      const unzip = new Zlib.Unzip(zipArr);
      const filePathListRaw = unzip.getFilenames();
      const filePathList = filePathListRaw.filter((path) => !path.match(/\/$|\.DS_Store|\.Thumbs.db/));

      filePathList.forEach((filePath) => {
        const configuration = { ...baseConfiguration };
        const urlPath = filePath;
        configuration.url = `http://aaa${urlPath}`;
        configuration.responseHeaders[0].value = mime.getType(path.basename(filePath));
        configuration.responsePayload = decoder.decode(unzip.decompress(filePath));
        composedConfiguration.configurations.push(configuration);
      });

      console.log(composedConfiguration);
    };
    zipReader.readAsArrayBuffer(file);
  });
});
